式提供資訊，也可能替代許多需要情緒互動的情境。機器人SILBOT的臉上搭載一台平板電腦，可以表現出各式各樣的表情，它被研發出來的目的是希望能與老人家作伴、預防失智症。韓國的腦神經內科醫師金乾荷醫師，進行以SILBOT預防失智症的實驗，結果顯示，使用機器人進行訓練的組別，減少的大腦皮質，只有完全沒做訓練那組的七分之一。隨著高齡化社會來臨，機器人可能開始扮演有如家人般的新角色，不過，這也伴隨著某種隱憂，如果機器人成為了家庭的一份子，是否也會破壞家人間本該有的緊密連帶？"),_c('br'),_c('br'),_vm._v("A.I.機器人也許未必要「取代」家人，也可能作為媒介，成為人際關係的潤滑劑。失智症患者的情緒反應與溝通問題，經常導致失智症患者的家人疲於照護，日本和臺灣的研究人員進行了一場新的實驗，他們將機器人TELENOID交到失智症長者手上，TELENOID竟成就了奇妙的催化效果——脾氣暴躁的老人家，開始放軟聲音唱起了情歌；憂鬱的老太太歡喜地對著TELENOID說話，還為它穿衣。TELENOID好像觸動了這些老人家心中柔軟的位置，激發他們想要照顧人的心情，很有潛力在未來可做為改善失智症長者與家人間關係的催化劑。\n          ")]),_vm._v(" "),_c('div',{staticClass:"series_direct",attrs:{"_ngcontent-c5":""}},[_vm._v("中山宙")]),_vm._v(" "),_c('ul',{staticClass:"series_category",attrs:{"_ngcontent-c5":""}},[_c('li',{attrs:{"_ngcontent-c5":""}},[_c('a',{attrs:{"_ngcontent-c5":"","href":"/channel/2/sub-genre/2-4"}},[_vm._v("4K精彩紀錄\n              ")])]),_vm._v(" "),_c('li',{attrs:{"_ngcontent-c5":""}},[_c('a',{attrs:{"_ngcontent-c5":"","href":"/channel/2/sub-genre/2-13"}},[_vm._v("時光台灣\n              ")])]),_vm._v(" "),_c('li',{attrs:{"_ngcontent-c5":""}},[_c('a',{attrs:{"_ngcontent-c5":"","href":"/channel/2/sub-genre/2-2"}},[_vm._v("人文探索\n              ")])]),_vm._v(" "),_c('li',{attrs:{"_ngcontent-c5":""}},[_c('a',{attrs:{"_ngcontent-c5":"","href":"/channel/2/sub-genre/2-1"}},[_vm._v("紀錄觀點\n              ")])]),_vm._v(" "),_c('li',{attrs:{"_ngcontent-c5":""}},[_c('a',{attrs:{"_ngcontent-c5":"","href":"/channel/2/sub-genre/2-9"}},[_vm._v("我們的島\n              ")])]),_vm._v(" "),_c('li',{attrs:{"_ngcontent-c5":""}},[_c('a',{attrs:{"_ngcontent-c5":"","href":"/channel/2/sub-genre/2-10"}},[_vm._v("獨立特派員\n              ")])]),_vm._v(" "),_c('li',{attrs:{"_ngcontent-c5":""}},[_c('a',{attrs:{"_ngcontent-c5":"","href":"/channel/2/sub-genre/2-6"}},[_vm._v("主題之夜\n              ")])]),_vm._v(" "),_c('li',{attrs:{"_ngcontent-c5":""}},[_c('a',{attrs:{"_ngcontent-c5":"","href":"/channel/2/sub-genre/2-3"}},[_vm._v("生態環境\n              ")])]),_vm._v(" "),_c('li',{attrs:{"_ngcontent-c5":""}},[_c