節目，觀眾將可以貼近台東原住民部落的原始生活；可以和老礦工在金瓜石野溪中一邊淘金一邊話當年；可以跟著返鄉青年在大火後的苑裡市場廢墟中找尋重生的可能；還可以和阿公阿嬤一起割稻、共餐，感受長輩們想要分享自己生活的那份熱情。"),_c('br'),_vm._v("藉由「台泰由你遊--Taiwan」的帶領，觀眾將可以和主持人一起走進台灣社區的客廳，不只作客，更可以和在地人一起思索台灣社區未來的各種可能。"),_c('br')]),_vm._v(" "),_c('ul',{staticClass:"series_category",attrs:{"_ngcontent-c5":""}},[_c('li',{attrs:{"_ngcontent-c5":""}},[_c('a',{attrs:{"_ngcontent-c5":"","href":"/channel/11/sub-genre/11-1"}},[_vm._v("戲劇攏佇遮\n              ")])]),_vm._v(" "),_c('li',{attrs:{"_ngcontent-c5":""}},[_c('a',{attrs:{"_ngcontent-c5":"","href":"/channel/11/sub-genre/11-2"}},[_vm._v("紀錄攏佇遮\n              ")])]),_vm._v(" "),_c('li',{attrs:{"_ngcontent-c5":""}},[_c('a',{attrs:{"_ngcontent-c5":"","href":"/channel/11/sub-genre/11-3"}},[_vm._v("兒少生活攏佇遮\n              ")])]),_vm._v(" "),_c('li',{attrs:{"_ngcontent-c5":""}},[_c('a',{attrs:{"_ngcontent-c5":"","href":"/channel/5/sub-genre/5-4"}},[_vm._v("4K潮流生活\n              ")])]),_vm._v(" "),_c('li',{attrs:{"_ngcontent-c5":""}},[_c('a',{attrs:{"_ngcontent-c5":"","href":"/channel/5/sub-genre/5-1"}},[_vm._v("生活探索\n              ")])])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row",attrs:{"_ngcontent-c5":""}},[_c('h4',{attrs:{"_ngcontent-c5":""}},[_vm._v("選集")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"series_episode_btn_bar d-block",attrs:{"_ngcontent-c5":""}},[_c('ul',{attrs:{"_ngcontent-c5":""}},[_c('li',{staticClass:"active",attrs:{"_ngcontent-c5":""}},[_c('a',{attrs:{"_ngcontent-c5":"","href":"javascript:void(0);"}},[_vm._v(" 1-2 ")])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"series_tab_area",attrs:{"_ngcontent-c5":""}},[_c('ul',{staticClass:"series_tab",attrs:{"_ngcontent-c5":""}})])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"modal fade",attrs:{"_ngcontent-c5":"","aria-